import React from 'react'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import PageModule from '../../../components/PageModule'
import { responsive } from '../../../components/ThemeProvider/theme'
import Charts from '../../Charts'

const data = [
  {
    title: '會員購票動能不減',
    desc: '2020年整體會員購票人數近20萬人，實質並未受疫情影響太多；其中新註冊會員比例約四成，相較2019年持平。',
    chart: ['8']
  },
  {
    title: '會員購票金額微幅下滑',
    desc: '2020年會員購票金額約7.1億，佔該年票房近8成，較前兩年高出約1成，而每人年平均購票金額則下降至3,610元。',
    chart: ['9']
  },
  {
    title: '年輕會員灌注活力',
    desc: '2020年購票1次的人數明顯高於前兩年，相應出雖因疫情限制活動，消費能量仍在2020下半年顯見回升；進一步觀察年購票1次的會員年齡，40歲以下購票人數有明顯提高。',
    chart: ['10-1', '10-2']
  },
  {
    title: '網路力量持續展現',
    desc: '2020年會員網路購票比例佔89%，於近幾年有逐年增加趨勢。',
    chart: ['11'],
    textAlign: responsive('center', 'left')
  },
]

const Page = ({ dataKey }) => {
  const content = data[dataKey]
  return (
    <PageModule
      left={
        <Box pr={responsive(0, '11em')}>
          <Text.H2 textAlign={content.textAlign || 'center'}>{content.title}</Text.H2>
          <Text fontSize={responsive('0.875em', '1em')} pt="0.625em">{content.desc}</Text>
        </Box>
      }
      leftProps={{
        maxWidth: responsive('100%', '49.6%'),
      }}
      right={<Charts data={content.chart} />}
    />
  )
}

export default Page
